let loadStatusMixin = {
  data () {
    return {
      list: [],
      page: 1,
      loadStatus: {
        type: 0,
        desc: '...'
      }
    }
  },
  methods: {
    onloadStart (desc) {
      this.loadStatus = {
        type: 0,
        desc: desc || '..'
      }
    },
    onLoadSuccess (desc) {
      this.loadStatus = {
        type: 1,
        desc: desc || ','
      }
    },
    onLoadError (desc) {
      this.loadStatus = {
        type: 2,
        desc: desc || ','
      }
      this.$refs.scroll.forceUpdate()
    }
  },
  activated () {
    if (this.$refs.scroll) {
      this.$refs.scroll.forceUpdate()
    }
  }
}
export default loadStatusMixin



